.wrapper-search {
  min-height: 1036px;
  max-height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.09);
  padding-bottom: 20px;
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: bblFadInOut 1.8s infinite ease-in-out;
}
.loader {
  color: #517cbe;
  font-size: 7px;
  position: absolute;
  display: none;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
  margin-left: 950px;
  margin-top: 50px;
}
.loader:before,
.loader:after {
  content: "";
  position: absolute;
  top: 0;
}
.loader:before {
  left: -3.5em;
  animation-delay: -0.32s;
}
.loader:after {
  left: 3.5em;
}

@keyframes bblFadInOut {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
.render-search p {
  color: #517cbe;
}
.render-search li {
  color: #0f8155;
  font-size: 15px;
  font-weight: 550;
}
.render-search li {
  font-size: 17px;
  margin-top: 10px;
}

.btn-search:before {
  color: #000;
  border: 3px;
  margin-top: 48px;
  margin-left: 10px;
  position: absolute;
  cursor: pointer;
  font-size: medium;
  font-weight: bolder;
}
.input-search {
  width: 300px;
  height: 30px;
  margin-left: 800px;
  margin-top: 40px;
  font-size: 15px;
  border-radius: 2px 2px 2px 2px;
  border: 2px solid;
  border-color: #517cbe;
}
.close {
  margin-top: -20px;
  margin-left: -20px;
  cursor: pointer;
}
.btn-search {
  margin-left: 1120px;
  margin-top: -25px;
  position: absolute;
}
.list-search {
  margin-top: 50px;
  margin-left: 280px;
}
.render-search {
  margin-top: 70px;
  margin-left: 260px;
}
.error-search {
  margin-top: 30px;
  margin-left: 500px;
  font-size: 18px;
  font-weight: bolder;
  color: #517cbe;
  font-size: 15px;
}
